import React from "react"
import closeIcon from "../img/cancel.png"
import {
  Box,
  Card,
  Title,
  InputContainer,
  Label,
  Input,
  NameContainer,
  Button,
  Text,
  BlueText,
  Close,
  CloseIcon,
  InputName,
} from "../styles/rgister"

class Rgister extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showModal: true,
    }
    this.handleShowModalChange = this.handleShowModalChange.bind(this)
  }

  handleShowModalChange(newShowModal) {
    this.setState({
      showModal: newShowModal,
    })
  }

  render() {
    const { showModal } = this.state
    return (
      <Box>
        {showModal === true && (
          <Card>
            <Close onClick={() => this.handleShowModalChange(null)}>
              <CloseIcon src={closeIcon} alt="close" />
            </Close>
            <InputContainer>
              <Title>Register</Title>
              <NameContainer>
                <div>
                  <Label>First Name</Label>
                  <InputName type="text" name="firstname" placeholder="snow" />
                </div>
                <div>
                  <Label>Last Name</Label>
                  <InputName type="text" name="lastname" placeholder="John" />
                </div>
              </NameContainer>
              <Label>Email</Label>
              <Input type="email" placeholder="John.snow@gmail.com" />
              <Label>Password</Label>
              <Input type="password" placeholder="********" id="password" />
              <Label>Confirm Password</Label>
              <Input
                type="password"
                placeholder="********"
                id="passwordConfirm"
              />
              <Button type="submit" value="Continue" />
              <Text>
                Already have an account?
                <BlueText>Sign in &gt;&gt;</BlueText>
              </Text>
            </InputContainer>
          </Card>
        )}
      </Box>
    )
  }
}

export default Rgister
